@font-face {
  font-family: "PT Sans";
  src: url("../../fonts/ptsans.woff2") format("woff2"), url("../../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../../fonts/ptsansbold.woff2") format("woff2"), url("../../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
::before,
::after {
  box-sizing: border-box;
}

.main-navigation {
  display: flex;
  justify-content: flex-end;
  min-height: 56px;
}

.main-navigation__list {
  font-family: "PT Sans", sans-serif;
}

.main-navigation__toggle {
  display: block;
  background-color: transparent;
  margin: 14px 14px;
  z-index: 3;
  border: none;
  position: absolute;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-navigation__icon--burger {
  fill: #81b3d2;
}
.main-navigation__icon--cross {
  fill: #ffffff;
}

@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__wrapper {
    display: none;
  }

  .main-navigation--closed .main-navigation__icon--burger {
    display: block;
  }

  .main-navigation--closed .main-navigation__icon--cross {
    display: none;
  }

  .main-navigation--opened .main-navigation__wrapper {
    position: absolute;
    display: block;
    width: 100%;
  }

  .main-navigation--opened .main-navigation__icon--burger {
    display: none;
  }

  .main-navigation--opened .main-navigation__icon--cross {
    display: block;
  }
}
.main-navigation--nojs .main-navigation__toggle {
  display: none;
}

.main-avigation--nojs .main-navigation__wrapper {
  position: static;
  display: block;
}

.main-navigation--opened .main-navigation__icon--burger {
  display: none;
}

.main-navigation__wrapper {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  z-index: 2;
}

