.field-text--phone, .field-text--email {
  position: relative;
}
.field-text--phone::before, .field-text--email::before {
  position: absolute;
  content: "";
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
  right: 0;
  top: 21px;
  z-index: 1;
}
.field-text--phone::after, .field-text--email::after {
  position: absolute;
  content: "";
  z-index: 1;
}
.field-text--phone::after {
  top: 41px;
  right: 20px;
  width: 18px;
  height: 18px;
  background-image: url("../../img/phone.svg.svg");
}
.field-text--email::after {
  top: 44px;
  right: 21px;
  width: 16px;
  height: 12px;
  background-image: url("../../img/mail.svg.svg");
}

